<template>
  <div class="w-100 style-container mt-5">
    <nav
      class="style-nav"
      :style="{
        'background-image': linearStyle,
      }"
      
    >
      <h3>{{ theme }}</h3>
      <div
        class="style-item"
        :class="{
          active: i === activeIndex,
        }"
        :data-type="arr[0]"
        v-for="(arr, i) in map"
        :key="i"
        @mouseover="activeIndex=i;activeType=arr[0]"
      >
        {{ arr[1] }}
      </div>
    </nav>
    <div class="style-content bg-white d-flex ">
      <div class="style-content-item" v-for="(item, i) in activeData" :key="i">
        <el-card>
          <img :src="item.image" class="image" />
          <div style="padding: 14px">
            <div class="text-center mb-3">{{ item.title }}</div>
            <div class="bottom clearfix">
              <span class="price">¥{{ item.price }}</span>
              <span class="address">{{ item.address }}</span>
            </div>
          </div>
        </el-card>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    linearStyle: {
      type: String,
      require: true,
    },
    allData: {
      type: Object,
    },

    theme: String,
  },
  data() {
    return {
      map: [
        ["all", "全部"],
        ["food", "约会聚餐"],
        ["spa", "丽人SPA"],
        ["movie", "电影演出"],
        ["travel", "品质出游"],
      ], // props 合理一些
      activeIndex: 0,
      activeType: "all",
    };
  },
  computed: {
    activeData() {
      return this.allData[this.activeType];
    },
  },
};
</script>

<style lang="less" scoped>
.style-container {
  .style-nav {
    height: 44px;
    line-height: 44px;
    display: flex;
    border-top-right-radius: 5px;
    border-top-left-radius: 5px;
    cursor: pointer;
    font-size: 14px;
    color: #fff;
    .style-item {
      padding: 0 5px;
      font-size: 14px;
      position: relative;
      &.active::after {
        content: "";
        position: absolute;
        width: 8px;
        height: 8px;
        transform: rotate(45deg);
        background-color: #fff;
        top: calc(100% - 4px);
        left: 50%;
      }
    }
    h3 {
      font-size: 18px;
      margin-left: 13px;
      margin-right: 10px;
      padding: 0 5px;
      font-weight: normal;
      height: 100%;
      line-height: 44px;
    }
  }
  .style-content {
    width: 100%;
    .style-content-item {
      width: 33.3%;
      .el-card {
        box-shadow: none;
        border: none;
        transition: background-color 0.5s;
        margin: 10px;
        &:hover {
          background-color: #f4f4f4;
        }
      }

      /deep/ .el-card__body {
        padding: 10px!important;
      }
      .price {
        font-size: 22px;
        color: #be9e4d;
      }
      .address {
        font-size: 12px;
        color: #999;
      }
      .bottom {
        span {
          display: inline-block;
          width: 50%;
          text-align: right;
        }
      }
      img {
        width: 100%;
        height: 208px;
      }
    }
  }
}
</style>