<template>
  <div class="w-100 menu-container">
    <dl class="position-relative">
      <dt>{{ title }}</dt>
      <dd
      @mouseleave="isActive = false"
        @mouseover="mouseover(item)"
        class="d-flex"
        v-for="(item, i) in menuList"
        :key="i"
      >
        <i :class="item.type"></i>{{ item.name }}
        <i class="right-ico el-icon-arrow-right"></i>
      </dd>
      <ShowItems @hide="isShow = $event" @show="isShow = $event" class="position-absolute" :itemList="showItemsList" v-show="isShow || isActive"></ShowItems>
    </dl>

  </div>
</template>

<script>
import ShowItems from "./ShowItem"; 
export default {
  props: {
    title: { type: String },
    menuList: {
      type: Array,
      require: true,
    },
  },
  components : {
    ShowItems
  },
  data() {
    return {
      showItemsList : {},
      isShow : false,
      isActive : false
    };
  },
  methods: {
    mouseover(item) {
      this.isActive = true
      this.showItemsList = item.items[0];
    },
  },
};
</script>

<style lang="less" scoped>
.menu-container {
  height: 475px;
  width: 230px;
  margin-left: 20px;
  margin-top: -50px;
  color: #fff;
  position: relative;
  background: linear-gradient(
    -180deg,
    rgba(2, 181, 157, 0.85) 2%,
    rgba(22, 146, 183, 0.85)
  );

  dl {
    dt {
      height: 50px;
      padding-top: 15px;
      font-size: 16px;
      font-weight: 400;
      margin-bottom: 10px;
      text-align: center;
    }
    dd {
      font-size: 13px;
      padding: 2px 12px;
      height: 26px;
      line-height: 26px;
      padding-left: 30%;
      cursor: pointer;
      &:hover {
        background: hsla(0, 0%, 100%, 0.2);
      }
      .right-ico {
        margin-left: auto;
        opacity: 1;
        transform: scale(0.8);
      }
      i {
        font-family: meituan;
        font-size: 13px;
        height: 100%;
        line-height: 26px;
        font-style: normal;
        opacity: 0.3;
        margin-right: 11px;
      }
    }
  }
}
.food:before {
  content: "\e622";
}
.takeout::before {
  content: "\e630";
}
.hotel::before {
  content: "\e62a";
}
</style>