<template>
  <div class="showItem" @mouseenter="mouseenter" @mouseleave="mouseleave">
    <dl>
      <dt>
        <router-link to="/index">{{ itemList.title }} </router-link>
        <router-link class="more" to="/more"
          >更多 <i class="el-icon-arrow-right"></i
        ></router-link>
      </dt>
      <dd v-for="(item, i) in itemList.items" :key="i">
        <router-link :to="`/index#${item}}`">{{ item }}</router-link>
      </dd>
    </dl>
  </div>
</template>

<script>
export default {
  props: {
    itemList: {
      type: Object,
      require: true,
    },
  },
  methods : {
      mouseleave () {
          this.$emit("hide",false);
      },
      mouseenter() {
          this.$emit("show",true);
      }
  }
};
</script>

<style lang="less" scoped>
@import "~@/css/common/var.less";
.showItem {
  z-index: 100;
  background-color: #fff;
  top: 60px;
  left: 220px;
  width: 400px;
  height: 416px;
  color: #666;
  padding: 0 30px;
  a:hover {
    color: @green!important;
  }
  dl {
    dt {
      margin-top: 24px;
      height: 22px;
      line-height: 22px;
      padding-bottom: 10px;
      box-sizing: content-box;
      border-bottom: 1px solid #e5e5e5;
      cursor: pointer;
      position: relative;
      a {
        font-size: 18px;
        font-weight: 500;
        color: #222;
      }
      .more {
        font-size: 12px;
        color: #999;
        position: absolute;
        right: 0;
      }
    }
    dd {
      display: inline-block;
      margin-right: 16px;
      margin-top: 10px;
      font-size: 12px;
    }
  }
}
</style>