<template>
  <div class="main-container mx-auto">
    <LayoutMain :leftWidth="230" :rightWidth="230">
      <template #left>
        <Menu :menuList="menuList" :title="'全部分类'"></Menu>
      </template>
      <template #main> <Center></Center> </template>
      <template #right><MLifeLogin></MLifeLogin></template>
    </LayoutMain>
  </div>
</template>

<script>
import LayoutMain from "@/components/LayoutMain";
import Menu from "@/components/Menu";
import Center from "./center";
import MLifeLogin from "@/components/MLifeLogin";
export default {
  components: {
    LayoutMain,
    Menu,
    Center,
    MLifeLogin
  },
  data() {
    return {
      menuList: [],
    };
  },
  async created() {
    let res = await this.$api.fetchMenu();
    this.menuList = res.data;
  },
};
</script>

<style lang="less" scoped>
.main-container {
  width: 1190px;
}
</style>