<template>
  <div class="w-100 d-flex">
    <div
      v-if="leftWidth > 0"
      class="left"
      :style="{
        width: leftWidth + 'px',
      }"
    >
      <slot name="left"></slot>
    </div>
    <div class="main">
      <slot name="main"></slot>
    </div>
    <div
      class="right"
      :style="{
        width: rightWidth + 'px',
      }"
    >
      <slot name="right"></slot>
    </div>
  </div>
</template>

<script>
export default {
  props: ["leftWidth", "rightWidth"],
};
</script>

<style lang="less" scoped>
.main {
  flex: 1 1 0;
}
</style>